import './NewGame.scss';
import { FC, memo, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Button, Form, Input, message, Select } from 'antd';

import { useAppDispatch, useAppSelector } from '../../redux/hooks';
import { deepCopy } from '../utilities';
import { addGame, Player } from '../../redux/ghiDiemSlice';
import moment from 'moment';



const NewGame: FC = () => {
  const dispatch = useAppDispatch()
  const ghiDiem = useAppSelector(state => state.ghiDiem)
  const navigate = useNavigate();
  const [form] = Form.useForm();

  const [players, setPlayers] = useState<Player[]>(
    [...new Array(4).keys()].map(i => ({
      id: crypto.randomUUID(),
      index: i,
      name: '',
    }))
  )









  return (
    <div className="New-game">
      <div className="Page-header">
        <h1 className='m4'>
          New Game
        </h1 >
      </div>

      <div className="Page-body">
        <Form
          form={form} layout='vertical'
          onFinish={values => {
            // kiểm tra trùng tên
            let existingNames: string[] = []
            for (const key in values) {
              if (key.startsWith('player-')) {

                let _playersName = values[key];
                if (existingNames.includes(_playersName)) {
                  message.error('Tên người chơi đã tồn tại')
                  return
                }

                existingNames.push(_playersName)
              }
            }


            let _players = deepCopy(players);

            for (let i = 0; i < _players.length; i++) {
              for (const key in values) {
                if (key.replace('player-', '') == _players[i].id) {
                  _players[i].name = values[key];
                }
              }
            }
            setPlayers(_players);

            let newGame_Id = crypto.randomUUID();
            dispatch(addGame({
              id: newGame_Id,
              name: moment().format('[Game] HHA-MM-DD-YYYY'),
              players: _players,
              rounds: [],
              createOn: moment().format('YYYY-MM-DD HH:mm:ss')
            }));
            navigate(`/games/${newGame_Id}/rounds`)
          }}
        >
          {/* chọn số players */}
          <Form.Item
            className='CAL02-head-filterCol'
            name='playerCount'
            label='Chọn só người chơi'
            initialValue={players.length}
          >
            <Select
              id="Select-players"
              value={players.length}
              onChange={e => {
                let _players = deepCopy(players);
                while (e < _players.length)
                  _players.pop();

                while (e > _players.length)
                  _players.push({
                    id: crypto.randomUUID(),
                    index: _players.length,
                    name: '',
                  });
                setPlayers(_players)
              }}
              options={[2, 3, 4, 5, 6].map(n => ({
                value: n,
                label: `${n} players`
              }))}
            />
          </Form.Item>

          {/* tên players */}
          {players.map(p =>
            <Form.Item key={p.id} name={`player-${p.id}`}
              rules={[{
                required: true, message: `Hãy nhập tên người chơi thứ ${p.index + 1}`
              }]}
              initialValue={p.name}
            >
              <Input className='Input-player-name' />
            </Form.Item>
          )}

          {/* submit */}
          <Button htmlType='submit' className='Button-submit'>
            Submit
          </Button>
        </Form>
      </div>

    </div>
  );
}

// why memo
export default memo(NewGame);
