import { PlusOutlined } from '@ant-design/icons';
import { Button, Col, Form, Input, message, Modal, Row } from 'antd';
import moment from 'moment';
import { FC, memo, useEffect, useState } from 'react';
import { useLocation, useNavigate, useParams, useSearchParams } from 'react-router-dom';
import { addNewRoundToGame, Game, Round } from '../../redux/ghiDiemSlice';
import { useAppDispatch, useAppSelector } from '../../redux/hooks';
import './Rounds.scss';


const Rounds: FC = () => {
  const dispatch = useAppDispatch()
  const ghiDiem = useAppSelector(state => state.ghiDiem)
  const location = useLocation();
  const [form] = Form.useForm();

  const [showModal, setShowModal] = useState(false);
  const [gameId, setGameId] = useState<string>();
  const [game, setGame] = useState<Game | undefined>();
  const [newScore, setNewScore] = useState<number[]>();

  useEffect(() => {
    const matches = location.pathname.match(/\/games\/(.*)\/rounds/)
    if (matches && matches.length > 1) {
      let gid = matches[1]
      setGameId(gid)
      let _game = ghiDiem.games.find(g => g.id == gid)
      setGame(_game)

      if (_game)
        setNewScore([...new Array(_game.players.length).keys()].map(_ => 0))
    }
  }, [])


  useEffect(() => {
    let _game = ghiDiem.games.find(g => g.id == gameId)
    if (_game) {
      setGame(_game)
      setNewScore([...new Array(_game.players.length).keys()].map(_ => 0))
    }
  }, [ghiDiem.games])


  function RenderTotalScore(rounds: Round[], length: number): JSX.Element {
    let scores = rounds.map(r => r.score)
    let totalScore =
      scores.reduce((previousValue, currentValue) => previousValue.map((v, i) => v = v + currentValue[i]),
        [...new Array(length).keys()].map(_ => 0))

    return <>
      {totalScore.map((s, i) => (
        <Col key={i} flex={1} className='tac'>
          <h3 className='Score'>{s}</h3>
        </Col>))}
    </>
  }

  function OnSubmit() {
    if (!newScore || !gameId || !game)
      return

    dispatch(addNewRoundToGame({
      gameId: gameId,
      round: {
        index: game.rounds.length || 0,
        score: newScore,
        createOn: moment().format('YYYY-MM-DD HH:mm:ss')
      }
    }))
    setShowModal(false)
  }



  return (
    <div className="Rounds">
      <div className="Page-header">
        <h1 className='m4'>
          {game?.name || `Game này ko tồn tại`}
        </h1>
      </div>

      <div className="Page-body">
        <Row>
          {game?.players.map(p => (
            <Col key={p.id} flex={1} className='Player'>
              <h3>{p.name}</h3>
            </Col>
          ))}
        </Row>

        {/* tổng điểm */}
        <Row>
          {game && RenderTotalScore(game.rounds, game.players.length)}
        </Row>

        {/* các vòng */}
        {game?.rounds.map(r => (
          <Row key={r.index}>
            {r.score.map((s, i) =>
              <Col key={i} flex={1} className='tac'>{s}</Col>
            )}
          </Row>
        ))}

        <Button
          className="New-round-fab"
          color="primary"
          onClick={() => setShowModal(true)}
          icon={<PlusOutlined />}
        />
      </div>







      {/* modal add score */}
      <Modal
        // title="Ghi điểm"
        className='Modal-add-score'
        visible={showModal}
        onCancel={() => setShowModal(false)}
        footer={[
          <Button
            key={'Button-submit'} type='primary'
            className='Button-submit mt4' size='large'
            onClick={OnSubmit}
          >
            Add
          </Button>
        ]}
      >
        {newScore?.map((n, i) =>
          <div key={i}>
            <h3 className='Player-name'>{game?.players[i]?.name}</h3>
            <Row className='Score-input-wrapper'>
              <Input
                defaultValue={0} value={n}
                className='Score-input' type='number' size='large'
              />
              <Button className='Reduce-button'
                onClick={() => {
                  let _newScore = [...newScore]
                  _newScore[i]--
                  setNewScore(_newScore)
                }}>−</Button>
              <Button type='primary' className='Increase-button'
                onClick={() => {
                  let _newScore = [...newScore]
                  _newScore[i]++
                  setNewScore(_newScore)
                }}>+</Button>
            </Row>
          </div>
        )}
      </Modal>
    </div >
  );
}

export default memo(Rounds);
